import React, {
  useState, useCallback, useContext, createContext,
} from 'react';
import PropTypes from 'prop-types';
import makeCSSBG from '../utils/make-css-bg';
import copyToClipboard from '../utils/copy-to-clipboard';
import saveAsset from '../utils/save-asset';

const CurrentAssetContext = createContext();

function useCurrentAsset() {
  const context = useContext(CurrentAssetContext);

  if (!context) {
    throw new Error('useCurrentAsset must be used within CurrentAssetProvider');
  }

  return context;
}

const propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

function CurrentAssetProvider({ children }) {
  const [currentAsset, setCurrentAsset] = useState(null);

  const copyHelper = useCallback(() => {
    if (currentAsset) {
      copyToClipboard(currentAsset.helper);
    }
  }, [currentAsset]);

  const copySVG = useCallback(() => {
    if (currentAsset) {
      copyToClipboard(currentAsset.svg);
    }
  }, [currentAsset]);

  const copyCSS = useCallback(() => {
    if (currentAsset) {
      copyToClipboard(makeCSSBG(currentAsset.svg));
    }
  }, [currentAsset]);

  const downloadSVG = useCallback(() => {
    if (currentAsset) {
      saveAsset(currentAsset);
    }
  }, [currentAsset]);

  return (
    <CurrentAssetContext.Provider
      value={{
        currentAsset,
        setCurrentAsset,
        copyHelper,
        copySVG,
        copyCSS,
        downloadSVG,
      }}
    >
      {children}
    </CurrentAssetContext.Provider>
  );
}

CurrentAssetProvider.propTypes = propTypes;

export { useCurrentAsset, CurrentAssetProvider };
