import React, { useContext, createContext } from 'react';
import PropTypes from 'prop-types';
import useAxios from 'axios-hooks';

const publicURL = typeof window !== 'undefined' && window.SVG_JAR_PUBLIC_URL
  ? window.SVG_JAR_PUBLIC_URL
  : '/';

const assetsFilePath = `${publicURL}svg-jar-viewer.json`;

const AssetsContext = createContext();

function useAssets() {
  const context = useContext(AssetsContext);

  if (!context) {
    throw new Error('useAssets must be used within AssetsProvider');
  }

  return context;
}

const propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

function AssetsProvider({ children }) {
  const [{ data, loading: isLoading, error }] = useAxios(assetsFilePath);

  return (
    <AssetsContext.Provider
      value={{
        ...(data || { assets: [] }),
        isLoading,
        isError: !!error,
      }}
    >
      {children}
    </AssetsContext.Provider>
  );
}

AssetsProvider.propTypes = propTypes;

export { useAssets, AssetsProvider };
