import React, { useState } from 'react';
import { useCurrentAsset } from '../../contexts/current-asset';
import getGrid from '../../utils/get-grid';
import CodeBlock from '../code-block';
import Tooltip from '../tooltip';
import Placeholder from '../placeholder';
import Dropdown from '../dropdown';
import { ReactComponent as FileIcon } from '../../images/icons/svg-file.svg';
import { ReactComponent as MoreIcon } from '../../images/icons/more.svg';
import { ReactComponent as PlaceholderIcon } from '../../images/icons/pane-placeholder.svg';
import './pane.scss';
import StatusButton from '../status-button';

function Pane() {
  const [previewScale, setPreviewScale] = useState(2);
  const {
    currentAsset,
    copyHelper,
    copySVG,
    copyCSS,
    downloadSVG,
  } = useCurrentAsset();

  return currentAsset
    ? (
      <div className="pane">
        <section className="pane__section">
          <div className="pane__content">
            <div className="pane__file">
              <FileIcon className="pane__file-icon" aria-hidden />

              <span className="pane__file-name">
                {currentAsset.fileName}
              </span>

              <Dropdown>
                <Dropdown.Trigger>
                  <button
                    className="btn btn--icon-only btn--small u-ml-auto"
                    type="button"
                    aria-label="Actions menu"
                  >
                    <MoreIcon />
                  </button>
                </Dropdown.Trigger>

                <Dropdown.Menu>
                  <Dropdown.Button onClick={copySVG}>
                    Copy SVG content
                  </Dropdown.Button>

                  <Dropdown.Button onClick={copyCSS}>
                    Copy CSS background
                  </Dropdown.Button>
                </Dropdown.Menu>
              </Dropdown>
            </div>
          </div>
        </section>

        <section className="pane__section">
          <header className="pane__header">
            <h2 className="pane__title">
              Preview
            </h2>

            <div className="range pane__range">
              <input
                type="range"
                max="5"
                min="0.5"
                step="0.1"
                value={previewScale}
                onChange={({ target }) => setPreviewScale(target.value)}
              />
            </div>
          </header>

          <div className="pane__content">
            <div className="pane__preview">
              <div
                style={{ transform: `scale(${previewScale})` }}
                dangerouslySetInnerHTML={{ __html: currentAsset.svg }}
              />
            </div>
          </div>
        </section>

        <section className="pane__section">
          <header className="pane__header">
            <h2 className="pane__title">
              Helper
            </h2>

            <StatusButton
              className="btn btn--small btn--primary"
              onClick={copyHelper}
            >
              Copy
            </StatusButton>
          </header>

          <div className="pane__content">
            <CodeBlock
              code={currentAsset.helper}
              language="handlebars"
            />
          </div>
        </section>

        <section className="pane__section">
          <header className="pane__header">
            <h2 className="pane__title">
              Info
            </h2>
          </header>

          <div className="pane__content">
            <ul>
              <li className="pane__info-item">
                <span className="pane__info-title">
                  Directory
                </span>

                {currentAsset.fileDir.length > 15
                  ? (
                    <Tooltip label={currentAsset.fileDir}>
                      <span className="pane__info-text">
                        {currentAsset.fileDir}
                      </span>
                    </Tooltip>
                  )
                  : (
                    <span className="pane__info-text">
                      {currentAsset.fileDir}
                    </span>
                  )}
              </li>

              <li className="pane__info-item">
                <span className="pane__info-title">
                  Grid size
                </span>
                <span className="pane__info-text">
                  {getGrid(currentAsset)}
                </span>
              </li>

              <li className="pane__info-item">
                <span className="pane__info-title">
                  File size
                </span>
                <span className="pane__info-text">
                  {currentAsset.fileSize}
                  {' '}
                  KB
                </span>
              </li>

              <li className="pane__info-item">
                <span className="pane__info-title">
                  Strategy
                </span>
                <span className="pane__info-text">
                  {currentAsset.strategy}
                </span>
              </li>
            </ul>
          </div>
        </section>

        <section className="pane__section pane__section--footer">
          <div className="pane__content">
            <button
              className="btn btn--full btn--outline"
              type="button"
              onClick={downloadSVG}
            >
              Download
            </button>
          </div>
        </section>
      </div>
    )
    : (
      <Placeholder
        className="pane"
        icon={PlaceholderIcon}
        title="No selection"
        text="Click an asset to see its info"
      />
    );
}

export default Pane;
