import React, { useRef, useCallback } from 'react';
import PropTypes from 'prop-types';
import AnimatedPopper from '../animated-popper';
import { useDropdown } from './dropdown';
import './dropdown.scss';

const propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  placement: PropTypes.string,
};

const defaultProps = {
  placement: 'bottom',
};

function Menu({ children, placement }) {
  const menuRef = useRef();
  const {
    dropdownID,
    isOpen,
    close,
    isTriggerActive,
  } = useDropdown();

  // Can't rely on `useEffect` for focus here
  // as we use react-spring animation
  const focusMenu = useCallback(() => {
    if (menuRef.current && isOpen) {
      menuRef.current.focus();
    }
  }, [isOpen]);

  return (
    <AnimatedPopper.Content
      className="dropdown"
      isShown={isOpen}
      placement={placement}
      onAnimationEnd={focusMenu}
    >
      {({ arrowProps, style }) => (
        <div className="dropdown__content" style={style}>
          <ul
            className="dropdown__list"
            id={dropdownID}
            role="menu"
            tabIndex="-1"
            ref={menuRef}
            onKeyDown={({ key }) => {
              if (key === 'Escape') {
                close();
              }
            }}
            onBlur={({ relatedTarget }) => {
              const isOwnMenu = menuRef.current.contains(relatedTarget);

              if (!isOwnMenu && !isTriggerActive.current) {
                close();
              }
            }}
          >
            {children}
          </ul>

          <div className="dropdown__arrow" {...arrowProps} />
        </div>
      )}
    </AnimatedPopper.Content>
  );
}

Menu.propTypes = propTypes;
Menu.defaultProps = defaultProps;

export default Menu;
