import React from 'react';
import { useQueryParam } from 'use-query-params';
import { useMedia } from '../../../contexts/media';
import { useDrawers } from '../../../contexts/drawers';
import { MEDIUM, LARGE } from '../../../breakpoints';
import TooltipButton from '../../tooltip-button';
import SearchBar from '../../search-bar';
import ArrowButton from '../../arrow-button';
import Dropdown from '../../dropdown';
import { ReactComponent as BurgerIcon } from '../../../images/icons/burger.svg';
import { ReactComponent as PaneIcon } from '../../../images/icons/pane.svg';
import './actions-bar.scss';

function ActionsBar() {
  const [sortBy, setSortBy] = useQueryParam('sort');
  const { toggleSidebar, togglePane } = useDrawers();
  const mediaMatches = useMedia();

  return (
    <div className="actions-bar">
      <section className="actions-bar__section">
        {!mediaMatches.includes(LARGE) && (
          <TooltipButton
            className="btn btn--icon-only actions-bar__item"
            label="Show sidebar"
            onClick={toggleSidebar}
          >
            <BurgerIcon />
          </TooltipButton>
        )}

        <SearchBar />
      </section>

      <section className="actions-bar__section">
        <Dropdown>
          <Dropdown.Trigger>
            <ArrowButton className="actions-bar__item">
              Sort by
            </ArrowButton>
          </Dropdown.Trigger>

          <Dropdown.Menu>
            <Dropdown.Button onClick={() => setSortBy(null)} active={!sortBy}>
              None
            </Dropdown.Button>

            <Dropdown.Button onClick={() => setSortBy('name')} active={sortBy === 'name'}>
              File name
            </Dropdown.Button>

            <Dropdown.Button onClick={() => setSortBy('grid')} active={sortBy === 'grid'}>
              Grid size
            </Dropdown.Button>
          </Dropdown.Menu>
        </Dropdown>

        {!mediaMatches.includes(MEDIUM) && (
          <TooltipButton
            className="btn btn--icon-only actions-bar__item"
            label="Show pane"
            onClick={togglePane}
          >
            <PaneIcon />
          </TooltipButton>
        )}

      </section>
    </div>
  );
}

export default ActionsBar;
