import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { QueryParamProvider } from 'use-query-params';
import { MediaProvider } from './contexts/media';
import { DrawersProvider } from './contexts/drawers';
import { AssetsProvider } from './contexts/assets';
import { CurrentAssetProvider } from './contexts/current-asset';
import App from './components/app';
import { BREAKPOINTS } from './breakpoints';
import './utils/init-is-tabbing';
import './styles/index.scss';

ReactDOM.render((
  <Router>
    <QueryParamProvider ReactRouterRoute={Route}>
      <MediaProvider breakpoints={BREAKPOINTS}>
        <DrawersProvider>
          <AssetsProvider>
            <CurrentAssetProvider>
              <App />
            </CurrentAssetProvider>
          </AssetsProvider>
        </DrawersProvider>
      </MediaProvider>
    </QueryParamProvider>
  </Router>
), document.getElementById('root'));
