import React from 'react';
import classNames from 'classnames';
import { useQueryParams, StringParam } from 'use-query-params';
import getGrid from '../../utils/get-grid';
import { useAssets } from '../../contexts/assets';
import { ReactComponent as FolderIcon } from '../../images/icons/folder-outline.svg';
import { ReactComponent as GridIcon } from '../../images/icons/grid-size.svg';
import './sidebar.scss';

function Sidebar() {
  const { assets } = useAssets();

  const [queryParams, setQueryParams] = useQueryParams({
    dir: StringParam,
    grid: StringParam,
  });

  const {
    dir: currentDir,
    grid: currentGrid,
  } = queryParams;

  const directories = assets
    .map(({ fileDir }) => fileDir)
    .filter((item, index, items) => items.indexOf(item) === index)
    .sort((a, b) => a.localeCompare(b));

  const grids = assets
    .map(({ gridWidth, gridHeight }) => ({ gridWidth, gridHeight }))
    .sort(({ gridWidth: aW, gridHeight: aH }, { gridWidth: bW, gridHeight: bH }) => {
      const byWidth = Number(aW) - Number(bW);
      return byWidth !== 0 ? byWidth : Number(aH) - Number(bH);
    })
    .map(asset => getGrid(asset))
    .filter((item, index, items) => items.indexOf(item) === index);

  function toggleQueryParam(param) {
    const [[key, value]] = Object.entries(param);
    const newValue = queryParams[key] === value ? null : value;
    setQueryParams({ [key]: newValue });
  }

  return (
    <div className="sidebar">
      <section className="sidebar__section">
        <button
          className={classNames('sidebar__item', {
            'is-active': !currentDir && !currentGrid,
          })}
          type="button"
          onClick={() => setQueryParams({ dir: null, grid: null })}
        >
          <FolderIcon className="sidebar__icon" aria-hidden />

          <span className="sidebar__text">
            All assets
          </span>
        </button>
      </section>

      {!!directories.length && (
        <section className="sidebar__section">
          <h2 className="sidebar__title">
            By directory
          </h2>

          <ul>
            {directories.map(dir => (
              <li key={dir}>
                <button
                  className={classNames('sidebar__item', {
                    'is-active': dir === currentDir,
                  })}
                  type="button"
                  onClick={() => toggleQueryParam({ dir })}
                >
                  <FolderIcon className="sidebar__icon" aria-hidden />

                  <span className="sidebar__text">
                    {dir}
                  </span>
                </button>
              </li>
            ))}
          </ul>
        </section>
      )}

      {!!grids.length && (
        <section className="sidebar__section">
          <h2 className="sidebar__title">
            By grid size
          </h2>

          <ul>
            {grids.map(grid => (
              <li key={grid}>
                <button
                  className={classNames('sidebar__item', {
                    'is-active': grid === currentGrid,
                  })}
                  type="button"
                  onClick={() => toggleQueryParam({ grid })}
                >
                  <GridIcon className="sidebar__icon" aria-hidden />

                  <span className="sidebar__text">
                    {grid}
                  </span>
                </button>
              </li>
            ))}
          </ul>
        </section>
      )}
    </div>
  );
}

export default Sidebar;
