import React, { forwardRef, useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useSpring, animated } from 'react-spring';
import { ReactComponent as CheckIcon } from '../../images/icons/check-mark.svg';
import './status-button.scss';

const StatusButton = forwardRef(
  function StatusButton({
    children,
    className,
    onClick,
    ...props
  }, ref) {
    const [isActive, setIsActive] = useState(false);
    const { opacity } = useSpring({
      opacity: isActive ? 1 : 0,
      onRest: () => isActive && setIsActive(false),
    });

    return (
      <button
        className={classNames('status-button', className)}
        type="button"
        onClick={(e) => {
          setIsActive(s => !s);
          onClick(e);
        }}
        ref={ref}
        {...props}
      >
        <animated.span
          className="status-button__content"
          style={{ opacity: opacity.interpolate(x => 1 - x) }}
        >
          {children}
        </animated.span>

        <animated.span
          className="status-button__icon"
          style={{
            opacity,
            transform:
              opacity
                .interpolate({
                  range: [0, 0.5, 1],
                  output: [0, 1.5, 1],
                })
                .interpolate(x => `scale(${x})`),
          }}
        >
          <CheckIcon aria-hidden />
        </animated.span>
      </button>
    );
  },
);

StatusButton.propTypes = {
  children: PropTypes.node.isRequired,
  onClick: PropTypes.func.isRequired,
  className: PropTypes.string,
};

StatusButton.defaultProps = {
  className: null,
};

export default StatusButton;
