import React, { memo } from 'react';
import { areEqual } from 'react-window';
import classNames from 'classnames';
import { assetType } from '../../../prop-types';
import { useCurrentAsset } from '../../../contexts/current-asset';
import './asset.scss';

const propTypes = {
  asset: assetType.isRequired,
};

const Asset = memo(function Asset({ asset, ...props }) {
  const { currentAsset, setCurrentAsset } = useCurrentAsset();

  return (
    <button
      className={classNames('asset', { 'is-active': asset === currentAsset })}
      type="button"
      onClick={() => setCurrentAsset(asset)}
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{ __html: asset.svg }}
      {...props}
    />
  );
}, areEqual);

Asset.propTypes = propTypes;

export default Asset;
