import React from 'react';
import { useMedia } from '../../contexts/media';
import { useDrawers } from '../../contexts/drawers';
import { useAssets } from '../../contexts/assets';
import { MEDIUM, LARGE } from '../../breakpoints';
import Drawer from '../drawer';
import GlobalShortcuts from '../global-shortcuts';
import ShortcutsBar from '../shortcuts-bar';
import TabBar from '../tab-bar';
import Sidebar from '../sidebar';
import Main from '../main';
import Pane from '../pane';
import Placeholder from '../placeholder';
import { ReactComponent as PlaceholderIcon } from '../../images/full-logo.svg';
import './app.scss';

function App() {
  const { isLoading, isError } = useAssets();
  const mediaMatches = useMedia();
  const {
    isSidebarOpen,
    isPaneOpen,
    isShortcutsOpen,
    closeSidebar,
    closePane,
    closeShortcuts,
  } = useDrawers();

  if (isLoading) {
    return (
      <Placeholder
        className="app app--placeholder"
        icon={PlaceholderIcon}
        title="Loading assets..."
      />
    );
  }

  if (isError) {
    return (
      <Placeholder
        className="app app--placeholder"
        icon={PlaceholderIcon}
        title="Unable to load assets file"
      />
    );
  }

  return (
    <div className="app">
      <GlobalShortcuts />
      <TabBar />

      {mediaMatches.includes(LARGE)
        ? (
          <Sidebar />
        )
        : (
          <Drawer
            placement="left"
            isOpen={isSidebarOpen}
            onClose={closeSidebar}
          >
            <Sidebar />
          </Drawer>
        )
      }

      <Main />

      {mediaMatches.includes(MEDIUM)
        ? (
          <Pane />
        )
        : (
          <Drawer
            placement="right"
            isOpen={isPaneOpen}
            onClose={closePane}
          >
            <Pane />
          </Drawer>
        )
      }

      <Drawer
        placement="bottom"
        isOpen={isShortcutsOpen}
        onClose={closeShortcuts}
      >
        <ShortcutsBar onClose={closeShortcuts} />
      </Drawer>
    </div>
  );
}

export default App;
