import React, { forwardRef } from 'react';
import { useQueryParams, StringParam } from 'use-query-params';
import AutoSizer from 'react-virtualized-auto-sizer';
import { FixedSizeGrid as Grid } from 'react-window';
import getGrid from '../../utils/get-grid';
import { useAssets } from '../../contexts/assets';
import Placeholder from '../placeholder';
import { ReactComponent as searchPlaceholder } from '../../images/icons/search-placeholder.svg';
import { ReactComponent as assetsPlaceholder } from '../../images/icons/assets-placeholder.svg';
import Asset from './asset';
import './assets.scss';

const cellSize = 64;
const padding = 16;

const sorters = {
  name({ fileName: a }, { fileName: b }) {
    return a.localeCompare(b);
  },

  dir({ fileDir: a }, { fileDir: b }) {
    return a.localeCompare(b);
  },

  grid({ gridWidth: aW, gridHeight: aH }, { gridWidth: bW, gridHeight: bH }) {
    const byWidth = Number(aW) - Number(bW);
    return byWidth !== 0 ? byWidth : Number(aH) - Number(bH);
  },
};

/* eslint-disable react/prop-types */
const GridInner = forwardRef(function GridInner({ style, ...rest }, ref) {
  return (
    <div
      style={{
        ...style,
        width: `${parseFloat(style.width) + padding}px`,
        height: `${parseFloat(style.height) + padding * 2}px`,
      }}
      ref={ref}
      {...rest}
    />
  );
});

function getFallbackID({ columnIndex, rowIndex }) {
  // Workaround for creating a unique key for a grid item
  // when there's no actual data.
  return columnIndex * rowIndex;
}

function Assets() {
  const { assets } = useAssets();

  const [queryParams] = useQueryParams({
    dir: StringParam,
    grid: StringParam,
    q: StringParam,
    sort: StringParam,
  });

  const {
    dir: dirFilter,
    grid: gridFilter,
    q: searchQuery,
    sort: sortBy,
  } = queryParams;

  let filteredAssets = [...assets];

  filteredAssets = dirFilter
    ? filteredAssets.filter(({ fileDir }) => fileDir === dirFilter)
    : filteredAssets;

  filteredAssets = gridFilter
    ? filteredAssets.filter(asset => getGrid(asset) === gridFilter)
    : filteredAssets;

  filteredAssets = searchQuery && searchQuery.length > 1
    ? filteredAssets.filter(({ fileName }) => fileName.includes(searchQuery))
    : filteredAssets;

  filteredAssets = Object.keys(sorters).includes(sortBy)
    ? filteredAssets.sort(sorters[sortBy])
    : filteredAssets;

  if (filteredAssets.length) {
    return (
      <div className="assets">
        <AutoSizer>
          {({ width, height }) => {
            const widthMinusPadding = width - padding * 2;
            const itemsCount = filteredAssets.length;
            const maxColumnCount = Math.trunc(widthMinusPadding / cellSize);
            const columnCount = Math.min(itemsCount, maxColumnCount);
            const rowCount = Math.ceil(itemsCount / columnCount);
            const cellSizeAddon = itemsCount >= maxColumnCount
              ? widthMinusPadding / columnCount - cellSize
              : 0;

            function getItem({ columnIndex, rowIndex }) {
              return filteredAssets[rowIndex * columnCount + columnIndex];
            }

            return (
              <Grid
                itemKey={(itemData) => {
                  const item = getItem(itemData);
                  return item ? item.id : getFallbackID(itemData);
                }}
                innerElementType={GridInner}
                width={width}
                height={height}
                columnCount={columnCount}
                columnWidth={cellSize + cellSizeAddon}
                rowCount={rowCount}
                rowHeight={cellSize + cellSizeAddon}
                overscanRowCount={1}
              >
                {({ style, ...itemData }) => {
                  const item = getItem(itemData);

                  if (!item) {
                    return null;
                  }

                  const itemStyle = {
                    ...style,
                    top: `${parseFloat(style.top) + padding}px`,
                    left: `${parseFloat(style.left) + padding}px`,
                  };

                  return <Asset style={itemStyle} asset={item} />;
                }}
              </Grid>
            );
          }}
        </AutoSizer>
      </div>
    );
  }

  const filtersText = [dirFilter, gridFilter]
    .filter(Boolean)
    .map(item => `<b>${item}</b>`)
    .join(' + ');

  if (searchQuery) {
    return (
      <Placeholder
        className="assets assets--placeholder"
        icon={searchPlaceholder}
        title="No assets were found"
        text={`Your search <b>${searchQuery}</b> did not match any assets${filtersText && ` at ${filtersText}`}`}
      />
    );
  }

  if (dirFilter || gridFilter) {
    return (
      <Placeholder
        className="assets assets--placeholder"
        icon={searchPlaceholder}
        title="No assets were found"
        text={`Can't find any assets at ${filtersText}`}
      />
    );
  }

  return (
    <Placeholder
      className="assets assets--placeholder"
      icon={assetsPlaceholder}
      title="Your assets library is empty"
      text="Drop some SVG files to your <b>public</b> directory"
    />
  );
}

export default Assets;
